
/**

 */
html {

}
.qubely-accordion-panel-handler-label {
	font-weight: 700;
}
.qubely-block-accordion .qubely-accordion-item {
	margin-bottom: 0.5rem;
}
.tooling {
	/*background-color: var(--wp--preset--tooling-base);*/
	border: none;
	font-size: 1rem;
	margin: 0;
	padding: 0;
	width: 100%;
}


.tooling-header {
	background-color: transparent;
	border-bottom: 2px solid black;
	/*color: var(--wp--preset--tooling-contrast);*/
	cursor: pointer;
	display: block;
	font-family: inherit;
	font-size: 0.9rem;
	font-weight: 700;
	margin: 0;
	outline: none;
	padding: 0.75rem 0 0.25rem 0;
	position: relative;
	text-transform: uppercase;
	width: 100%;
}

mark {
	background-color: #ffebeb;
}

.tooling-header:focus {
	outline: 4px solid transparent;
}


.tooling-list {
	padding: 0.75rem ;
	border-bottom: 1px solid darkgray;
}
.tooling-list:nth-of-type(even) {
	background: #f6f6f6;
}
.tooling-docs {
	display: flex;
	align-items: center;
	justify-content: flex-start;
	padding-left: 0.4rem;
	font-size: 0.9rem;
}
.tooling-desc {
	display: flex;
	flex-direction: column;
	font-size: 0.9rem;
	line-height: 1.2;
}
.tooling-desc > mark {
	margin-top: 0.4rem;
	padding: 0.1rem 0.2rem;
}
.tooling-name {
	line-height: 1.2;
	font-weight: 600;
}
mark {
	width: fit-content;
}
.pdf:before {
	content: 'PDF';
	color: white;
	padding:0 0.4rem;
	border-radius: 0.5rem;
	left: -5px;
	position: relative;
	background: #ff2828;
	font-size: x-small;
	font-weight: bold;

}
.stl:before {
	content: 'STL';
	color: black;
	padding:0 0.4rem;
	border-radius: 0.5rem;
	left: -5px;
	position: relative;
	background: #b9dce7;
	font-size: x-small;
	font-weight: bold;

}

.gcode:before {
	content: 'GCODE';
	color: black;
	padding:0 0.4rem;
	border-radius: 0.5rem;
	left: -5px;
	position: relative;
	background: #a6eca6;
	font-size: x-small;
	font-weight: bold;

}


a.tooling-btn {
	align-self: center;
	line-height: 1;
	white-space: nowrap;
}
a.tooling-btn:hover {
	cursor: pointer;
}

.wp-block-post-content h3 + .tooling-panel {
	margin-top: 0 !important;
}

.acf-tooling-block-empty-state {
	padding: 1rem 1rem;
}

.tooling-header > .heading-tool,
.tooling-list > .tooling-name {
	grid-area: item;
}
.tooling-header > .heading-docs,
.tooling-list > .tooling-docs {
	grid-area: docs;
}
.tooling-header > .heading-desc,
.tooling-list > .tooling-desc {
	grid-area: desc;
}
.tooling-header,
.tooling-list {
	display: inline-grid;
	grid-template-areas: "item item docs docs desc desc desc desc";
	grid-template-columns: repeat(8, 1fr);

	width: 100%;
}

.tool-footer {
	display: flex;
	flex-grow: 1;
	justify-content: space-around;
	align-items: center;
	width: 100%;
	background: #1F1F1F;
}
.light-bkg {
	background-color: ivory;
}

/* wrap description */
@media (max-width: 999px) {

	.tooling-header,
	.tooling-list {
		display: grid;
		grid-template-areas: "item item item item docs docs docs docs"
                          "desc desc desc desc desc desc desc desc";
		width: 100%;
	}

	.tooling-header > .heading-desc {
		display: none;
	}

	a.tooling-btn {
		align-self: center;
		padding: 0.2rem;
		background-image: none;
	}
	.tooling-desc {
		padding-top: 1rem;
	}
}
@media (max-width: 479px) {
	.tooling-name {
		padding: 0.5rem 0;
	}
	.tooling-header,
	.tooling-list {
		display: grid;
		grid-template-areas:  "item item item item item item item item"
                          "docs docs docs docs docs docs docs docs"
                          "desc desc desc desc desc desc desc desc";
		width: 100%;
	}
	.tooling-header > .heading-desc {
		display: none;
	}
	.tooling-btn {

	}
}


